.dom-slider {
  overflow: hidden;
  opacity: 0;
  transition: 0.5s;
  position: relative;
  width: 100%;
}
.dom-slider .draggable {
  position: relative;
}
.dom-slider .draggable.in-drag {
  cursor: grabbing;
}
.dom-slider.init {
  opacity: 1;
}
.dom-slider .slider-arrow {
  width: 45px;
  height: 45px;
  justify-content: center;
  background-color: transparent;
  border: none;
  transition: 0.3s;
  cursor: pointer;
  display: inline-block;
  position: absolute;
  top: calc(50% - 45px / 2);
  display: flex;
  align-items: center;
  justify-content: ceter;
  border-radius: 50%;
  border: 2px solid #fff;
}
.dom-slider .slider-arrow:after {
  display: block;
  text-align: center;
  width: 100%;
  height: 100%;
  color: #fff;
  font-weight: bold;
  font-size: 20px;
  line-height: 0;
  padding-bottom: 2px;
}
.dom-slider .slider-arrow:focus {
  outline: none;
}
.dom-slider .prev-arrow {
  left: 10px;
}
.dom-slider .prev-arrow:after {
  content: "<";
}
.dom-slider .next-arrow {
  right: 10px;
}
.dom-slider .next-arrow:after {
  content: ">";
}
.dom-slider .slider-outher {
  width: auto;
  display: flex;
  align-items: stretch;
  will-change: transform;
}
.dom-slider .slide {
  flex-shrink: 0;
  flex-grow: 0;
}
.dom-slider .slider-dots {
  position: absolute;
  bottom: 15px;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.dom-slider .dots-slider {
  display: block;
}
.dom-slider .slide-dot {
  flex-shrink: 0;
  flex-grow: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: transparent;
  border: 1px solid #fff;
  padding: 0;
  display: block;
  margin: 0 5px;
  cursor: pointer;
  transition: 0.3s;
}
.dom-slider .slide-dot:focus {
  outline: none;
}
.dom-slider .slide-dot.active-dot {
  transform: scale(1.2);
  background-color: #fff;
}
.dom-slider.variable-height .slider-outher {
  align-items: center;
  height: 100%;
}
