  color: #fff;
}
a,
a:visited {
  color: #fff;
  transition: 0.3s;
  text-decoration: none;
}
a.bnt,
.btn,
button.btn,
input.btn,
.wpcf7-submit.btn {
  border: none;
  transition: 0.3s;
  cursor: pointer;
}
a.bnt:focus,
.btn:focus,
button.btn:focus,
input.btn:focus,
.wpcf7-submit.btn:focus {
  outline: none;
}
::selection {
  background-color: rgba(255, 255, 255, 0.8);
