  flex-shrink: 0;
}
.site-wrap .footer {
  margin-top: auto;
}
.main-content > * {
  margin-bottom: 120px;
}
.ico {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ico > * {
  width: 22px !important;
  height: auto !important;
}
.ico.ico-life > * {
  width: auto !important;
  height: 26px !important;
}
.ico-left {
  position: relative;
  padding-left: 34px;
}
.ico-left .ico {
  position: absolute;
  left: 0;
  top: calc(50% - 24px / 2);
}
.svg-fill,
.svg-stroke {
  transition: 0.3s;
}
.opacity-load {
  opacity: 0;
  transition: 0.5s;
}
h1,
